.portfolio-inner {
  grid-template-rows: 1fr;
  grid-template-columns: 20rem 12rem 8fr;
  align-items: center;
  display: grid;
  overflow: hidden;
}

.portfolio-timeline-outer {
  grid-template-rows: calc(25vh - 6rem) 6rem;
  justify-content: center;
  align-self: flex-end;
  justify-items: center;
  height: 100vh;
  display: grid;
  transform: translate(0, 100vh);
}

.portfolio-timeline-circle-outer {
  aspect-ratio: 1;
  background-image: conic-gradient(var(--gray-text-color) 100%, transparent 100%);
  border-radius: 3rem;
  width: 6rem;
  display: grid;
}

.portfolio-timeline-circle-inner {
  background-color: var(--main-bg-color);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  margin: 4px;
  display: flex;
}

.portfolio-timeline-circle-inner > span {
  color: #fff;
  font-size: 2em;
  font-weight: 700;
}

.portfolio-timeline-line {
  background-image: linear-gradient(var(--gray-text-color) 100%, transparent 100%);
  width: 4px;
  height: 100%;
}

.portfolio-info-outer {
  grid-template-rows: 1fr 2fr 2fr;
  grid-template-columns: 6fr 6fr;
  align-self: flex-end;
  height: calc(75vh + 5.5rem);
  display: grid;
}

.portfolio-info-title {
  color: #fff;
  font-size: 4em;
  font-weight: 900;
  transform: translate(200%);
}

.portfolio-info-description {
  color: var(--gray-text-color);
  font-size: 2em;
  transform: translate(200%);
}

.portfolio-stack-outer {
  transform: translate(200%);
}

.portfolio-stack-title {
  color: #fff;
  font-size: 3em;
  font-weight: 600;
}

.portfolio-stack-description {
  color: #fff;
  font-size: 2em;
}

